.container {
  width: 100%;
  height: 380px;
  background-image: linear-gradient(
      to right bottom,
      rgba(8, 29, 38, 0.9),
      rgba(38, 88, 105, 0.9)
    ),
    url('https://www.themoviedb.org/t/p/w355_and_h200_multi_faces/8vNQ3i2Xa3mrCKn6eq2ce5hhegT.jpg');
   opacity: 0.5; 
  background-position: center center;
  background-size: cover;
  padding-top: 20px;
  background-repeat: no-repeat;
  image-rendering: pixelated;
  z-index: 21;
  display: flex;
  flex-direction: row;
  overflow-x: scroll;
  overflow-y: hidden;
  transition: height 0.5s linear;
}

.text-style {
  font-size: 24px;
  font-family: 'Source Sans Pro', Arial, sans-serif;
  margin: -128px 10px 0px 46px;
  white-space: nowrap;
  position: absolute;
  color: #fff;
}

.container::-webkit-scrollbar {
  width: 14px; /* width of the entire scrollbar */
  height: 8px;
}

.container::-webkit-scrollbar-track {
  background: transparent; /* color of the tracking area */
}

.container::-webkit-scrollbar-thumb {
  background-color: rgba(226, 224, 224, 0.8); /* color of the scroll thumb */
  border-radius: 7px; /* roundness of the scroll thumb */
  border: 1px solid white; /* creates padding around scroll thumb */
}

/* .card-style {
  display: flex;
  align-items: start;
  position: absolute;
} */

@media (max-width: 800px) {
  .container {
    height: 380px;
    padding-top: 0;
    z-index: 21;
    display: flex;
    overflow-x: scroll;
    overflow-y: hidden;
    transition: all 0.5s linear;
  }

  .text-style {
    font-size: 24px;
    font-family: 'Source Sans Pro', Arial, sans-serif;
    margin: -128px 10px 0px 25px;
    white-space: nowrap;
    position: absolute;
    color: #fff;
  }
  .card-tekst {
    font-size: 24px;
    font-family: 'Source Sans Pro', Arial, sans-serif;
    margin: 39px 29px 0px 41px;
    white-space: nowrap;
    color: #fff;
  }
}

.card-style {
  height: 2rem;
  align-items: center !important;
  margin-right: 2px;
}
