.toggle {
  margin-left: 12px;
  height: 29px;
  width: 200px;
  border: 1px solid #081c24;
  border-radius: 14px;
  background-color: white;
  position: relative;
  cursor: pointer;
}

/* .toggle.switch {
  width: 200px;
  background-color: white;
} */

.toggle-flex {
  display: flex;
  justify-content: flex-start;
  align-items: stretch;
  align-content: center;
  border-radius: 30px;
}

.toggle-onTv {
  background: #01d277;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.3rem;
  z-index: 3;
  margin: 0;
  display: inline-flex;
  align-content: center;
  align-items: center;
  justify-content: center;
  font-size: 1em;
  padding: 4px 20px;
  margin-bottom: -29px;
  white-space: nowrap;
}

.toggle-onTheatre {
  background: #081c24;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.3rem;
  z-index: 3;
  margin: 0;
  display: inline-flex;
  align-content: center;
  align-items: center;
  justify-content: center;
  font-size: 1em;
  padding: 4px 20px;
  margin-bottom: -29px;
  white-space: nowrap;
}

.notch {
  border-radius: 30px;
  background-color: #081c24;
  /* color: linear-gradient(to right, #c0fecf 0%, #1ed5a9 100%); */
  position: absolute;
  top: 0px;
  left: -2px;
  z-index: 1;
  transition: all 0.3s ease;
  height: 29px;
  width: 76px;
}

.switch > .notch {
  height: 29px;
  width: 110px;
  background: #081c24;
  position: absolute;
  top: 0px;
  left: -8px;
  /* z-index: 1; */
  transition: all 0.3s ease;
  transform: translate(100px, 0);
}

.switch {
  background-color: white;
}

.tog > .toggle-onTheatre {
  background-color: #01d277;
}
.tog > .toggle-onTv {
  background-color: #081c24;
}

@media (max-width: 800px) {
  .toggle {
    margin-left: 10px;
    /* margin-top: 90px; */
    height: 36px;
    width: 153px;
    border-radius: 30px;
  }

  .toggle-flex {
    border-radius: 15px;
    position: absolute;
    top: -15px;
    left: -8px;
  }

  .toggle-onTv {
    margin: 0;
    font-size: 1rem;
    padding: 7px 2px 7px 12px;
    margin-bottom: -68px;
  }

  .toggle-onTheatre {
    margin: 0;
    font-size: 1rem;
    padding: 2px 14px;
    margin-bottom: -68px;
  }

  .notch {
    border-radius: 30px;
    position: absolute;
    top: 0px;
    left: 0px;
    height: 36px;
    width: 60px;
  }

  .switch > .notch {
    height: 36px;
    width: 96px;
    position: absolute;
    top: 0px;
    left: -43px;
  }
}
