.image-containers img {
  border-radius: 8px;
  box-shadow: 0 2px 8px rgb(0 0 0 / 10%);
  margin: 10px;
}

.img-size {
  width: 150px;
  height: 225px;
  cursor: pointer;
  transition: block 5s ease-in;
}

.vert-container {
  display: flex;
  flex-direction: column;
  margin-top: -20px;
  margin-bottom: 20px;
}

.vertical-title {
  color: #000;
  font-size: 1rem;
  font-weight: bold;
  margin: 0 0 0 20px;
}

.vertical-title:hover {
  color: #01b4e4;
  cursor: pointer;
}

.vertical-date {
  color: #3131318e;
  margin: 5px 0 0 20px;
}
