.welcome-page {
  display: flex;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  transition: all 0.5s;
  height: calc(100vh / 3.5);
  margin-top: 60px;
}

.welcome-container {
  display: flex;
  width: 100%;
  flex-direction: column;
  justify-content: center;
  padding-left: 24px;
  /* background-color: red; */

  /* flex-direction: column; */
  /* justify-items: center; */
  /* padding: 24px; */
}

.welcome-container > .welcome-heading {
  font-size: 3em;
  font-weight: 700;
  line-height: 1.2;
  margin: 0;
  font-family: 'Source Sans Pro', Arial, sans-serif;
  color: aliceblue;
}
.welcome-container > .welcome-subHeading {
  font-size: 2em;
  font-weight: 600;
  margin-top: 0;
  line-height: 1.4;
  color: aliceblue;
  font-family: 'Source Sans Pro', Arial, sans-serif;
}

@media (max-width: 800px) {
  .welcome-page {
    /* padding-top: 66px; */
  }
  .welcome-container {
    /* margin-top: 3rem; */
  }
  .welcome-container > .welcome-subHeading {
    font-size: 1.5em;
  }
}
